.recharts-tooltip-wrapper {
  .custom-tooltip {
    width: 200px;
    margin: 0;
    line-height: 24px;
    border: 1px solid #f5f5f5;
    background-color: rgb(255, 255, 255);
    padding: 10px;

    .label {
      margin: 0;
      color: #666;
      font-weight: 700;
    }
    .desc {
      margin: 0;
      color: #999;
    }
    .intro {
      border-top: 1px solid #f5f5f5;
      margin: 0;
    }
  }
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 25px;
}

.card {
  background-color: rgb(240, 240, 240);
}
